import React from 'react';
import PropTypes from 'prop-types'
import styled from 'styled-components';

const LetterWrapper = styled.span`
display: block;
height: 1.5rem;
width: 1.5rem;
font-size: 2rem;
margin-left: -.2rem;
`


const letter = ({ letter }) => {

    return (
        <LetterWrapper>

            {letter.isShowed ? letter.name : "_"}
        </LetterWrapper>
    );
}


export default letter;